import { useState, useEffect, useCallback } from 'react';

/**
 * Debounces value changes, which can be useful when performing expensive operations on state change. An example is when you want to send a network request on state change.
 *
 * @param value The value to debounce.
 * @param delay The delay time in ms the value should be debounced.
 */
const useDebouncedValue = (value, delay) => {
    const [debouncedValue, setDebouncedValue] = useState(value);
    useEffect(() => {
        const timer = setTimeout(() => setDebouncedValue(value), delay ?? 500);
        return () => {
            clearTimeout(timer);
        };
    }, [value, delay]);
    return debouncedValue;
};

/**
 * Manages boolean state. It provides open, close and toggle handlers and accepts optional onOpen and onClose callbacks. It can be used to manage controlled modals, popovers and other similar components.
 *
 * @param initialState Controls whether the component's initial state is open or closed.
 * @param callbacks Contains onOpen and onClose callbacks that are called when the open or close handlers are called.
 */
const useDisclosure = (initialState, callbacks) => {
    const [isOpen, setIsOpen] = useState(initialState);
    const open = () => {
        if (!isOpen) {
            setIsOpen(true);
            callbacks?.onOpen?.();
        }
    };
    const close = () => {
        if (isOpen) {
            setIsOpen(false);
            callbacks?.onClose?.();
        }
    };
    const toggle = () => {
        if (isOpen)
            close();
        else
            open();
    };
    return { isOpen, handlers: { open, close, toggle } };
};

/**
 * Subscribes to a media query and returns a boolean value indicating whether the media query matches.
 *
 * @param query The media query to match.
 */
const useMediaQuery = (query) => {
    const [matches, setMatches] = useState(getInitialValue(query));
    const onMediaQueryChange = useCallback((event) => {
        setMatches(event.matches);
    }, []);
    useEffect(() => {
        if (typeof window?.matchMedia === "function") {
            const mediaQueryList = window.matchMedia(query);
            setMatches(mediaQueryList.matches);
            mediaQueryList.addEventListener("change", onMediaQueryChange);
            return () => mediaQueryList.removeEventListener("change", onMediaQueryChange);
        }
        return undefined;
    }, [query, onMediaQueryChange]);
    return matches;
};
const getInitialValue = (query) => {
    if (typeof window !== "undefined" && typeof window?.matchMedia === "function") {
        return window.matchMedia(query).matches;
    }
    return false;
};

/**
 * Subscribes to color scheme preference changes and returns dark if dark scheme is preferred and light if light scheme is preferred.
 */
const usePrefersColorScheme = () => {
    return useMediaQuery("(prefers-color-scheme: dark)") ? "dark" : "light";
};

/**
 * Subscribes to reduced motion preference changes and returns true if reduced motion is preferred and false if not.
 */
const usePrefersReducedMotion = () => {
    return useMediaQuery("(prefers-reduced-motion: reduce)");
};

export { useDebouncedValue, useDisclosure, useMediaQuery, usePrefersColorScheme, usePrefersReducedMotion };
